// theme colors
$blue-dark-c4g: #103a5d;
$blue: lighten($blue-dark-c4g, 10%);

$primary: $blue-dark-c4g;

$link-color: $blue;

// fonts
@import "fonts_custom";

$font-family-sans-serif: "Lato";
$headings-font-family-script: 'Dancing Script';
$headings-font-weight: normal;

$font-size-base: 1.3rem;

$enable-responsive-font-sizes: true;
$enable-rounded: false;

@import "../../../schmiederohling/scss/bootstrap453/variables";

// some vars have to be insertet right after the import of bootstrap vars because they are unknown before

// tiles (showcase-tiles or offer-tiles)
$border-tile: darken($light, 10%);

$headings-margin-top: $spacer * 2;

// modify the widths just to eliminate to much white space
$container-max-widths: (
        sm: 576px,
        md: 768px,
        lg: 992px,
        xl: 1200px
);






