#cboxNext,
#cboxPrevious,
#cboxClose {
  text-indent: initial;
  background-image: none;
  background-color: rgba(#ffffff, .6);

  &:hover {
    background-color: rgba(#ffffff, 1);
  }
}

#cboxClose {
  height: 38px;
}

#cboxOverlay {
  background: #ffffff;
}

#cboxLoadedContent {
  border-color: #ffffff;
}

#cboxContent {
  background: #fff;
}