/**
 * styles for detail page like storewindow page
 */

// mandatory
@import "variables";

.detail-view {

  .imageGallery {
    &-item {
      @include make-col-ready();
      @include make-col(6);
      max-height: 150px;
      overflow: hidden;
      margin-bottom: 15px;

      &.first {
        @include make-col(12);
        max-height: 100%;
      }
    }
  }

  #detail-section-4 {
    margin-bottom: 20px;

    > .container {
      > .row {
        .detail-field-text {

          border-bottom: 1px solid #ebebeb;
          padding-bottom: 1rem;

          > div {
            @extend .h4;
            font-weight: bold;
          }

        }
      }
    }
  }

  // Besonderheiten
  .detail-field-tags {

    .tags {
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    .tag-entry {
      .detail-field-text {
        display: none;
      }
    }
  }

  .detail-field-text {

    &.price {
      //margin-bottom: 20px;

      > span {
        background: #ebebeb;
        border-radius: 3px;
        position: relative;
        padding: 10px 10px 10px 20px;
        margin-left: 20px;
        font-size: 20px;

        &:before {
          content: "\f153";
          font-family: 'Font Awesome 5 Free';
          font-size: 20px;
          color: var(--gray);
          font-weight: 700;
          position: absolute;
          top: -20px;
          left: -20px;
          width: 40px;
          height: 40px;
          background: #ebebeb;
          border-radius: 50%;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          border: 2px solid #fff;
        }
      }
    }

    &.displayType,
    &.diet,
    &.cuisine {
      > div {
        @extend .h4;
        font-weight: bold;
      }

      border-bottom: 1px solid #ebebeb;
      padding-bottom: 1rem;
    }

    &.diet,
    &.cuisine {
      grid-column: span 12 / span 12;

      @media screen and (min-width: 34.375em) {
        grid-column: span 6 / span 6;
      }

      @media screen and (min-width: 48em) {
        grid-column: span 4 / span 4;
        //@extend .col-4;
      }
    }

  }

  .tag-fields {
    margin-top: 30px;


    > div {
      display: inline-block;
      margin: 10px 10px 0 0;
    }

    .detail-field-text span {
      display: block;
    }

    a {
      text-transform: uppercase;
      background-color: $primary;
      color: #fff;
      display: inline-block;
      font-weight: 400;
      text-align: center;
      white-space: nowrap;
      vertical-align: middle;
      border: 1px solid transparent;
      padding: 0.375rem 0.75rem;
      font-size: 1rem;
      line-height: 1.5;
      border-radius: 0.25rem;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;


      &:hover {
        background-color: $secondary;
      }
    }


    .tag_corona {

      a {
        color: #333333;
        text-decoration: underline;
        text-transform: none;
        background-color: transparent;

        text-align: left;
        white-space: normal;
        vertical-align: inherit;
        border: none;
        padding: 0 0;
        font-size: inherit;
        line-height: normal;
        border-radius: 0;
        transition: none;

        &:hover {
          color: #333333;
          text-decoration: none;
        }
      }
    }

    .tag_menu {
      a {
        background-color: #F6921E;

        &:hover {
          background-color: darken(#F6921E, 10%);
        }
      }
    }

    .tag_online_reservation {
      a {
        background-color: #B073D9;

        &:hover {
          background-color: darken(#B073D9, 10%);
        }
      }
    }

    .tag_onlineshop {
      a {
        background-color: #ADBD11;

        &:hover {
          background-color: darken(#ADBD11, 10%);
        }
      }
    }

    .tag_delivery {
      a {
        background-color: #FF7BAB;

        &:hover {
          background-color: darken(#FF7BAB, 10%);
        }
      }
    }

  }

  .embed-responsive-wrapper {
    padding: 20px;
    background-color: $light;
    border-radius: 5px;

    .embed-responsive {
      max-width: 700px;
      margin: 0 auto;
      border: 10px solid #ffffff;
      border-radius: 5px;
    }
  }

  .opening-hours-day-entry {
    display: flex;
    margin: -5px;
    flex-wrap: wrap;

    .opening-hours-day {
      min-width: 112px;
    }

    .opening-hours-day,
    .opening-hours-day-interval {
      margin: 5px;
    }
  }
}