.slider-control {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  margin: -10px;

  .slider-prev,
  .slider-next,
  .slider-menu {
    position: relative;
    top: auto;
    font-size: 2.5rem;
  }

  .slider-prev,
  .slider-next {
    margin: 10px;
    color: #FFFFFF;
    text-shadow: 0 0 10px #000000;
  }

  .slider-menu {
    width: auto;
    left: auto;
    margin-left: 0;
    margin: 10px;

    b {
      color: #FFFFFF;
      text-shadow: 0 0 10px #000000;

      &.active {
        color: #000000;
        text-shadow: 0 0 10px #FFFFFF;
      }
    }
  }
}