.image_container {
  img {
    @include img-fluid;
  }
}

@mixin cols_x {
  @for $i from 1 through $grid-columns {
    .cols_#{$i} {
      list-style: none;
      padding-left: 0;

      @include make-row();
      @include row-cols($i);

      > * {
        @include make-col-ready();
      }
    }
  }
}

@include cols_x;